'use client';

import { useState, useRef, useEffect } from 'react';

export default function PrikbordPage() {
  const [isLoading, setIsLoading] = useState(false);
  const [memes, setMemes] = useState([]);
  const fileInputRef = useRef<HTMLInputElement>(null);

  useEffect(() => {
    console.log('useEffect triggered');
    loadMemes();
  }, []);

  const loadMemes = async () => {
    try {
      console.log('Loading memes...');
      const response = await fetch('/api/prikbord');
      const result = await response.json();
      
      if (result.success) {
        setMemes(result.data);
      }
    } catch (error) {
      console.error('Error loading memes:', error);
    }
  };

  const handleFileUpload = (event: React.ChangeEvent<HTMLInputElement>) => {
    const file = event.target.files?.[0];
    if (file) {
      console.log('File selected:', file.name);
      // For now, just log the file selection
      alert(`File selected: ${file.name}`);
    }
  };

  if (isLoading) {
    return (
      <div className="min-h-screen flex items-center justify-center">
        <div className="text-lg">Laden...</div>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-gray-100">
      <div className="bg-white shadow-sm border-b p-4">
        <div className="max-w-7xl mx-auto flex items-center justify-between">
          <h1 className="text-2xl font-bold text-gray-800">Prikbord - Memes</h1>
          <div className="flex items-center gap-4">
            <button
              onClick={() => fileInputRef.current?.click()}
              className="px-4 py-2 bg-blue-500 text-white rounded hover:bg-blue-600"
            >
              PNG Uploaden
            </button>
          </div>
        </div>
      </div>

      <div className="p-8">
        <p>Memes count: {memes.length}</p>
        <div className="mt-4">
          {memes.map((meme: any) => (
            <div key={meme.id} className="bg-white p-4 rounded shadow mb-2">
              <p>ID: {meme.id}</p>
              <p>Title: {meme.title}</p>
              <p>Image: {meme.image_url}</p>
            </div>
          ))}
        </div>
      </div>

      <input
        ref={fileInputRef}
        type="file"
        accept="image/png,image/jpeg,image/jpg"
        onChange={handleFileUpload}
        className="hidden"
      />
    </div>
  );
}